<template>
    <div class="counter-component">
      <div class="counter-btn" @click.stop="minus"> - </div>
      <div class="counter-show">
        <input type="text" v-model="number" @keyup="fixNumber">
      </div>
      <div class="counter-btn" @click.stop="add"> + </div>
    </div>
</template>

<script>
export default {
  props: {
    max: {
      type: Number,
      default: 5
    },
    min: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      number: this.min
    }
  },
  watch: {
    number () {
      this.$emit('change', this.number);
    }
  },
  methods: {
    fixNumber () {
      let fix;
      if (typeof this.number === 'string') {
        fix = Number(this.number.replace(/\D/g, ''));
      } else {
        fix = this.number;
      }
      if (fix > this.max || fix < this.min) {
        fix = this.min;
      }
      this.number = fix;
    },
    minus () {
      if (this.number <= this.min) {
        return;
      }
      this.number--;
    },
    add () {
      if (this.number >= this.max) {
        return;
      }
      this.number++;
    }
  }
}
</script>

<style scoped>
.counter-component {
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
}
.counter-show {
  float: left;
}
.counter-show input {
  border: none;
  border-top: 1px solid #e3e3e3;
  border-bottom: 1px solid #e3e3e3;
  height: 34px;
  line-height: 34px;
  width: 40px;
  outline: none;
  text-indent: 4px;
}
.counter-btn {
    border: 1px solid #e3e3e3;
    float: left;
    height: 34px;
    line-height: 34px;
    width: 36px;
    font-size: 20px;
    font-weight: bold;
    color: #97a8be;
    text-align: center;
    cursor: pointer;
}
.counter-btn:hover {
  border-color: #4db3ff;
  background: #4db3ff;
  color: #fff;
}

</style>



